.planContainer {
  display: flex;
  flex-wrap: wrap;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}
.no-padding{
  padding: 0;
}
.plan {
  background: white;
  width: 18em;
  box-sizing: border-box;
  text-align: center;
  
  margin-bottom: 1em;
  border-radius: 0 0 7px 7px;
  &:first-child{
    margin-left: 0;
  }
  &:last-child{
    margin-right: 0;
  }
}
.infoContainer{
  background-image: linear-gradient(
    180deg
    , #ffffff 0%, #ececec 51%, #ffffff 238%) !important;
}
.planContainer{
  padding-bottom: 129px;
}
.buss{
  .infoContainer
  {
    background-image: linear-gradient(
  180deg
  , #ffffff 0%, #bbbbbb 51%, #ffffff 238%) !important;
    }
 
}
.plan .titleContainer {
  background-color: #f3f3f3;
  
  border-radius: 7px 7px 0 0;
  background-image: linear-gradient(
    180deg
    , #707070 0%, #404040 51%, #969696 238%);
  padding: 1em;
}
.plan .titleContainer .title {
  font-size: 1.45em;
 
  color: #FFF;
  font-weight: 700;
}
.plan small{
  font-size: 8px;
}
.plan .infoContainer {
  padding: 1em 2em;
  color: #2d3b48;

  background: #F6F6F6;
  box-sizing: border-box;
  box-shadow: 4px 4px 13px 0px rgb(232,232,232);
}
.plan .infoContainer .price {
  font-size: 1.35em;
  padding: 1em 0;
  font-weight: 600;
  margin-top: 0;
  display: inline-block;
  width: 80%;
}
.plan .infoContainer .price p {
  font-size: 1.35em;
  display: inline-block;
  margin: 0;
}
.plan .infoContainer .price span {
  font-size: 1.0125em;
  display: inline-block;
}
.plan .infoContainer .desc {
  padding-bottom: 1em;
  border-bottom: 2px solid #f3f3f3;
  margin: 0 auto;
  width: 90%;
}
.plan .infoContainer .desc em {
  font-size: 1em;
  font-weight: 500;
}
.plan .infoContainer .features {
  font-size: 1em;
  list-style: none;
  padding-left: 0;
}
.plan .infoContainer .features li {
  padding: 1.4em;
  border-bottom: 1px solid #707070;
  font-size: 14px;
  font-weight: 700;
  font-family: Roboto;

}
.plan .infoContainer .selectPlan {

  padding: 0.75em 1em;
  border-radius: 1em;
  cursor: pointer;
  transition: all 0.25s;
  margin: 1em auto;
  box-sizing: border-box;
  max-width: 70%;
  display: block;
  font-weight: 700;
  color:#fff;
  background: #000000;
}
.plan .infoContainer .selectPlan:hover {

  color: white;
}
