:root {
  --white: #fff;
  --black: #000;
  --bg: #f8f8f8;
  --grey: #999;
  --dark: #1a1a1a;
  --light: #e6e6e6;
  --wrapper: 1000px;
  --blue: #00b0ff;
}
.chat-wrappers{
  
  height: 500px;
  overflow: hidden;

  .left {
      float: left;
      width: 37.6%;
      height: 100%;
    
      background-color: var(--white);
      .top {
          position: relative;
          width: 95%;
          /* padding: 15px; */
          margin: 10px 0;
          height: 42px;
          &:after {
              position: absolute;
              bottom: 0;
              left: 50%;
              display: block;
              width: 80%;
              height: 1px;
              content: '';
              background-color: var(--light);
              transform: translate(-50%, 0);
          }
      }
      input {
          float: left;
          width:100%;
          height: 42px;
          padding: 0 15px;
          border: 2.5px solid #CECACA;
          background-color:transparent;
          border-radius: 10px;
          @include font();
          &:focus {
              outline: none;
          }
      }
      a.search {
          display: block;
          float: left;
          width: 42px;
          height: 42px;
          margin-left: 10px;
          border: 1px solid var(--light);
          background-color: var(--blue);
          background-image: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/382994/name-type.png');
          background-repeat: no-repeat;
          background-position: top 12px left 14px;
          border-radius: 50%;
      }
      .people {
          margin-left: -1px;
       
          border-left: 1px solid var(--light);
          padding-right: 10px;
          width: calc(100% + 2px);
          overflow: auto;
          height: 100%;
          .person {
            text-align: left;
              position: relative;
              width: 100%;
              padding: 12px 10% 16px;
              cursor: pointer;
              background-color: #DEDEDE;
              &:nth-child(odd){
                background-color: #EEEEEE;
             
              }
              &:after {
                  position: absolute;
                  bottom: 0;
                  left: 50%;
                  display: block;
                  width: 80%;
                  height: 1px;
                  content: '';
                  background-color: var(--light);
                  transform: translate(-50%, 0);
              }
              img {
                  float: left;
                  width: 40px;
                  height: 40px;
                  margin-top: 5px;
                  margin-right: 12px;
                  border-radius: 50%;
                                      object-fit: cover;
              }
              .rounder__count__text{
                position: absolute;
                right: 8px;
                top: 50%;
                border-radius: 50%;
                /* padding: 10px; */
                transform: translateY(-50%);
                background: #76C00D;
                height: 36px;
                width: 36px;
                display: flex;
                align-items: center;
                justify-content: center;
                color: #fff;
              }
              .name {
                  font-size: 14px;
                  line-height: 22px;
                  color: var(--dark);
                  @include font-bold;
              }
              .time {
                  font-size: 14px;
                  position: absolute;
                  bottom: 12px;
                  right: 15%;
                  padding: 0 0 5px 5px;
                  color: var(--grey);
               
              }
              .preview {
                  font-size: 14px;
                  display: inline-block;
                  overflow: hidden !important;
                  width: 70%;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  color: var(--grey);
              }
              &.active,&:hover {
                  margin-top: -1px;
                  margin-left: -1px;
                  padding-top: 13px;
                  border: 0;
                  background-color: var(--blue);
                  width: calc(100% + 2px);
                  padding-left: calc(10% + 1px);
                  span {
                      color: var(--white);
                      background: transparent;
                  }
                  &:after {
                      display: none;
                  }
              }
          }
      }
  }
  .right {
      position: relative;
      float: left;
      width: 62.4%;
      height: 100%;
      padding-left: 10px;
      .top {
          width: 100%;
          height: 47px;
          padding: 15px 29px;
          background-color: #eceff1;
          span {
              font-size: 15px;
              color: var(--grey);
              .name {
                  color: var(--dark);
                  @include font-bold;
              }
          }
      }
      .chat {
          position: relative;
          display: none;
          overflow: hidden;
          padding: 10px 10px 24px;
          border-width: 1px 1px 1px 0;
          border-style: solid;
          border-color: var(--light);
          height: calc(100% - 48px);
          background-color: #F6F6F6;
          justify-content: flex-end;
          flex-direction: column;
          &.active-chat {
              display: block;
              height: 100%;
              display: flex;
              .bubble {
                  transition-timing-function: cubic-bezier(.4,-.04, 1, 1);
                  color: #000000;
                  background-color: #fff;
                  @for $i from 1 through 10 {
                      &:nth-of-type(#{$i}) {
                          animation-duration: .15s * $i;
                      }
                  }
              }
          }
      }
      .write {
          position: absolute;
          bottom: 8px;
          left: 10px;
          height: 42px;
          padding-left: 8px;
          border: 1px solid #707070;
          background-color: #FFFFFF;
          width:98%;
          border-radius: 5px;
          input {
              font-size: 16px;
              float: left;
              width: 347px;
              height: 40px;
              padding: 0 10px;
              color: var(--dark);
              border: 0;
              outline: none;
          
              @include font;
          }
          .write-link {
              &.attach {
                 
              }
              &.smiley {
                 
              }
              &.send {
                 
              }
          }
      }
      .bubble {
          font-size: 14px;
          position: relative;
          display: inline-block;
          clear: both;
          margin-bottom: 8px;
          padding: 13px 14px;
          vertical-align: top;
          border-radius: 5px;
          /*&:before {
              position: absolute;
              top: 19px;
              display: block;
              width: 8px;
              height: 6px;
              content: '\00a0';
              transform: rotate(29deg) skew(-35deg);
          }*/
          &.you {
              float: left;
              color: var(--white);
              background-color: var(--blue);
              align-self: flex-start;
           
              &:before {
                  left: -3px;
                  background-color: var(--blue);
              }
              
          }
          &.me {
              float: right;
              color: var(--dark);
              background-color: #DEDEDE !important;
              align-self: flex-end;
           
              &:before {
                  right: -3px;
                  background-color: #DEDEDE;
              }
          }
          &.date {
            float: right;
            color: #CECACA;
            background-color: transparent !important;
            align-self: flex-end;
            padding: 2px 10px!important;
            color: #CECACA !important;
            font-weight: 500;
   
            &:before {
                right: -3px;
               
            }
        }
        &.date-you {
          float: left;
          color: #CECACA;
          background-color: transparent !important;
          align-self: flex-end;
          padding: 2px 10px!important;
          color: #CECACA !important;
          font-weight: 500;
 
          &:before {
              right: -3px;
             
          }
      }
      }
      .conversation-start {
          position: relative;
          width: 100%;
          margin-bottom: 27px;
          text-align: center;
          span {
              font-size: 14px;
              display: inline-block;
              color: var(--grey);
              &:before,&:after {
                  position: absolute;
                  top: 10px;
                  display: inline-block;
                  width: 30%;
                  height: 1px;
                  content: '';
                  background-color: var(--light);
              }
              &:before {
                  left: 0;
              }
              &:after {
                  right: 0;
              }
          }
      }
  }

}

